.moderation__ {
       margin-top: 50px;
}

.main_content.dual_subheader_content {
       margin-top: 60px;
}

.modCard_tagProduct {
       transition: all 0.25s;
}

.mtagProduct_remove {
       padding: 0 3.5px;
       z-index: 9;
       opacity: 0;
       visibility: hidden;
       transition: all 0.25s;
}

.modCard_tagProduct:hover .mtagProduct_remove {
       opacity: 1;
       visibility: visible;
}

.content_video video,
.content_video iframe {
       height: 100%;
       // width: auto !important;
       width: 100%;
       object-fit: contain;
       // object-fit: cover;
       object-position: center;
}


.blob {
       background-color: var(--bs-white);
       border-radius: 50%;
       display: inline-block;
       margin: 0;
       cursor: pointer;
       transform: scale(1);
}

.blob.blob_001 {
       animation: pulse-dark 2s infinite;
       box-shadow: 0 0 0 0 var(--bs-white);
       height: 16px;
       width: 16px
}

.blob.blob_002 {
       box-shadow: 0 0 1px var(--bs-dark);
       text-align: center;
       transition: .6s;
       height: 20px;
       width: 20px
}

.ugc-deviceupdate img{
       height: 20px;
       width: 20px;
}

@keyframes pulse-dark {
       0% {
              box-shadow: 0 0 0 0 #000000b3;
              transform: scale(.85)
       }

       70% {
              transform: scale(1)
       }

       99% {
              box-shadow: 0 0 0 8px #0000
       }

       to {
              box-shadow: 0 0 0 0 #0000;
              transform: scale(.85)
       }
}

@media (min-width: 992px) {

       .edit_media_ [data-id="uploaded-image"],
       .edit_media_ {
              width: calc(100vw - 550px);
              height: calc(100vh - 160px);
              object-fit: contain;
       }

}

@media (max-width: 991px) {
       .mod_actions {
              position: fixed;
              bottom: 0;
              padding: 10px;
              width: 100%;
              left: 0;
              justify-content: center;
              background-color: var(--bs-white);
       }
}

   .title-description{
       display: block;
       font-weight: bold;
       padding-bottom: 0.4rem;
   }