.moderation-editThumbnail{
    &>div{
        &>.bg-white.w-100.rounded-1{
            position: relative;
            padding: 10px;
            background-color: var(--bs-gray-100) !important;
            border-radius: 6px;
            border: 1px solid var(--bs-gray-200);
            width: 100%;
            height: 350px;
            .object-fit-contain{
                width: 100%;
                height: 100% !important;
            }
            img, video{
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
                border-radius: 4px;
                max-height: 100% !important;
            }
            div{
                top: 10px;
                width: auto !important;
                height: auto !important;
                align-items: baseline;
                right: 0;
                justify-content: end;
                .btn.btn-icon{
                    background-color: var(--bs-gray-100);
                    &:last-child{
                        margin-right: 20px !important;
                    }
                }
            }
        }       
    }
}